import React, { useState } from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import styles from "./styles.module.scss";
import { faPaperclip } from "@fortawesome/free-solid-svg-icons";
import { useChangePhoto } from "../../hooks/useChangePhoto";

export const ChangePhotoForm = ({ layoutClick, handleFormClick }) => {
  const [fileName, setFileName] = useState("");
  const { postImage } = useChangePhoto();

  const handleDragOver = (e) => {
    e.preventDefault();
  };

  const handleDrop = (e) => {
    e.preventDefault();
    const files = e.dataTransfer.files;
    console.log("Files dropped:", files);
  };

  const handleFileChange = (e, componentId) => {
    e.stopPropagation();
    if (e.target.files && e.target.files.length > 0) {
      const file = e.target.files[0];
      setFileName(file.name); // Save the file name
      postImage(file, "5");
      console.log("Files selected:", file);
    }
  };

  const handleSubmit = (e) => {
    e.preventDefault(); // Prevent the default form submission
    const fileInput = document.querySelector('input[type="file"]');
    const file = fileInput.files[0];
    if (file) {
      postImage(file);
    }
  };

  return (
    <div className={styles.layout}>
      <form
        className={styles.image_form}
        onDragOver={handleDragOver}
        onClick={handleFormClick}
        onDrop={handleDrop}
        onSubmit={handleSubmit}
      >
        <FontAwesomeIcon
          className={styles.image_form__attachment}
          icon={faPaperclip}
        />
        {fileName ? (
          <div className={styles.image_form__text}>{fileName}</div>
        ) : (
          <span className={styles.image_form__text}>
            Поместите сюда фото, на которое вы хотите заменить текущее
          </span>
        )}
        <input
          type="file"
          onChange={(e) => handleFileChange(e, "editable-agro-image-4")}
          name="editable-image"
          className="file-input"
          style={{
            position: "absolute",
            width: "100%",
            height: "100%",
            top: 0,
            left: 0,
            opacity: 0,
            cursor: "pointer",
          }}
        />
        <button className={styles.button} type="submit">
          Сохранить
        </button>
      </form>
    </div>
  );
};
