import React from "react";
import { useEffect, useState } from "react";
import { Fade } from "react-awesome-reveal";
import { useFetchContent } from "../../hooks/useFetchContent.js";
import { Link } from "react-scroll";
import axios from "axios";
import Menu from "../../components/Menu/Menu.jsx";
import Header from "../../components/Header/Header.jsx";
import Loader from "../../components/Loadingscreen/LoadingScreen.jsx";
import ButtonMore from "../../components/Buttons/ButtonMore/ButtonMore.jsx";
import Field from "../../assets/wheat_main.webp";
import { ChangePhotoForm } from "../../components/ChangePhotoForm/index.jsx";

import "./main.scss";

const MainScreen = () => {
  const { content } = useFetchContent();
  const [isHovering, setIsHovering] = useState(false);

  const handleShowOnClick = (e) => {
    setIsHovering(true);
    e.stopPropagation();
  };

  const handleFormClick = (e) => {
    e.stopPropagation(); // This prevents click event from reaching the layout
  };

  const handleMouseOut = (e) => {
    setIsHovering(false);
  };

  const closeMenu = () => {
    setIsOpen(false);
  };

  const [isOpen, setIsOpen] = useState(false);

  function toggleMenu() {
    setIsOpen(!isOpen);
  }

  useEffect(() => {
    console.log(isOpen);
    if (isOpen) {
      document.body.style.overflowY = "hidden";
    } else {
      document.body.style.overflowY = "";
    }

    return () => {
      document.body.style.overflowY = "";
    };
  }, [isOpen]);

  return (
    <section className="main-screen-mobile">
      <Loader />
      <main className="main" id="main">
        <Header isOpen={isOpen} toggleMenu={toggleMenu} />
        {isOpen && (
          <Menu isOpen={isOpen} closeMenu={closeMenu} toggleMenu={toggleMenu} />
        )}
        <div className="main__content">
          <div className="main__text-section">
            <Fade direction="up" delay={200} triggerOnce>
              <h1 className="main__heading">
                <span className="main__span" id="editable-agro-1">
                  {content && content["1"]
                    ? content["1"].value
                    : "Content not found"}
                </span>
                <strong id="editable-text-2">
                  {" "}
                  {content && content["2"]
                    ? content["2"].value
                    : "Content not found"}
                </strong>
              </h1>
            </Fade>
            <Fade direction="up" delay={300} t iggerOnce>
              <p className="main__paragraph" id="editable-agro-2">
                {content && content["3"]
                  ? content["3"].value
                  : "Content not found"}
              </p>
            </Fade>
          </div>
          <div className="main__button">
            <Fade direction="up" delay={400} triggerOnce>
              <Link smooth to="about">
                <ButtonMore text={"Подробнее"} className={"outline"} />
              </Link>
            </Fade>
          </div>
          <div className="main__button-pc">
            <Fade direction="up" delay={400} triggerOnce>
              <Link smooth to="about-pc">
                <ButtonMore text={"Подробнее"} className={"outline"} />
              </Link>
            </Fade>
          </div>
        </div>
        <div onClick={handleShowOnClick} className="image-container">
          <img
            src={
              content && content["5"] ? content["5"].value : "Content not found"
            }
            onClick={handleShowOnClick}
            alt="icon"
            className="main__field"
            id="editable-agro-image-4"
          />
        </div>
        {isHovering && (
          <ChangePhotoForm
            layoutClick={handleMouseOut}
            handleFormClick={handleFormClick}
          />
        )}
      </main>
    </section>
  );
};

export default MainScreen;
